import React, { useContext } from 'react'
import { Navigate, useLocation, useNavigate } from 'react-router-dom'
import notecontext from '../context/notecontext'
const CurrentItem = () => {
  const context = useContext(notecontext)
  const { currobj,countryFullName } = context
  const { name, region, population, flag, languages, currencies, topLevelDomain, capital, subRegion, nativeName} = currobj
  let navigate=useNavigate()
  let location=useLocation()
  
  const goback=()=>{
    // console.log("hello")
    console.log(location.pathname)
    navigate('/')
  }
  return (
    <>
      <div className="container d-flex flex-column vh-100 mt-5 ">
        <button className="btn btn-primary" onClick={goback}>Go back</button>
        <div className="card mb-3 mt-5" >
          <div className="row g-0">
            <div className="col-md-4">
              <img src={flag} className="img-fluid rounded-start" alt="..." />
            </div>
            <div className="col-md-8 ">
              <div className="d-flex flex-wrap">
                <div className="card-body col-md-4">
                  <h5 className="card-title">{name}</h5>
                  <p>
                    Native Name: {nativeName} <br />
                    Population: {population} <br />
                    Region: {region} <br />
                    Sub Region: {subRegion} <br />
                    Capital: {capital}

                  </p>
                </div>
                <div className="card-body col-md-4 mt-3">
                  <p>
                    Top Level Domain: {topLevelDomain[0]} <br />
                    Currencies: {currencies[0].name} <br />
                    Language: {languages[0].name} <br />

                  </p>
                </div>
              </div>
              <div className="lower d-flex">
                Lorem, ipsum.
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default CurrentItem
